.App-wrapper {
  width: 1200px;
  margin: 0 auto;
  display: grid;
  grid-template-areas: 
  "header header"
  "nav content";
  grid-template-rows: 120px 1fr;
  grid-template-columns: 2fr 12fr;
  grid-gap: 10px;
  background-color: red;



  &-content {
    grid-area: content;
    background-color: crimson;
  
  }
}



